@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;600&display=swap');

/* font-family: 'Manrope', sans-serif; */
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    @variants responsive {
      .masonry-3-col {
        column-count: 3;
        column-gap: 1em;
      }
      .masonry-2-col {
        column-count: 2;
        column-gap: 1em;
      }
      .break-inside {
        break-inside: avoid;
      }
    }
  }
